  #classicformpage h6 {
    line-height: 1.7;
  }
  
  #classicformpage .navbar {
    transition: background 0.5s ease-in-out, padding 0.5s ease-in-out;
  }
  
  #classicformpage .top-nav-collapse {
    background: #424f95 !important;
  }
  
  @media (max-width: 768px) {
    #classicformpage .navbar:not(.top-nav-collapse) {
      background: #424f95 !important;
    }
  }
  
  #classicformpage label {
    color: #fff !important;
  }
  .results {
    color: #ff8800;
    background-color: #fff;
    width: 300px;
    text-align: center;
  }
  body {
    background-image: url("https://github.com/hungle913/images/blob/master/ClickyGame/DIY/DIY_Background.jpg?raw=true");
    background-repeat: repeat;
    background-position: center;
    background-size: contain;
}